small) {\n          display: none;\n        }\n      }\n    }\n\n    @media (max-width: $breakpoint-small) {\n      width: 100%;\n      padding: var(--spacing-s) var(--spacing-xs);\n    }\n  }\n\n  .card {\n    a {\n      color: var(--color-text);\n    }\n  }\n  .section__flex {\n    display: flex;\n    align-items: unset;\n\n    .qr__wrapper {\n      background: var(--color-background);\n      padding: var(--spacing-s);\n      border-radius: var(--border-radius);\n      margin-right: var(--spacing-m);\n\n      .qr-code {\n        width: 206px !important;\n        height: 206px !important;\n\n        canvas {\n          width: 100% !important;\n          height: 100% !important;\n        }\n      }\n    }\n\n    .address__wrapper {\n      display: flex;\n      justify-content: center;\n      margin-top: var(--spacing-s);\n      width: 100%;\n      font-size: var(--font-xxxsmall);\n\n      fieldset-section {\n        width: 100%;\n\n        input,\n        button {\n          padding: var(--spacing-s) var(--spacing-xxxs);\n          height: 16px;\n          background-color: var(--color-header-button);\n        }\n      }\n    }\n  }\n\n  .credit-amount {\n    font-size: var(--font-body);\n  }\n  .card__actions {\n    align-items: end;\n  }\n  .empty {\n    .spinner {\n      border: unset;\n      background: unset;\n    }\n  }\n\n  .columns {\n    display: flex;\n    height: 100%;\n    gap: var(--spacing-s);\n    align-items: stretch;\n    flex-wrap: wrap;\n\n    .column {\n      position: relative;\n      display: inline-block;\n      flex: 1;\n      flex-shrink: 0;\n      margin: 0;\n      min-height: 100%;\n      min-width: 380px;\n    }\n  }\n\n  .card--background {\n    background-color: var(--color-header-button);\n    border-radius: var(--border-radius);\n    padding: var(--spacing-s);\n    height: 100%;\n\n    .section-content__wrapper {\n      display: flex;\n      flex-direction: column;\n      min-height: 100%;\n\n      .section__warning {\n        padding: var(--spacing-m);\n        margin-top: auto;\n        background: rgba(255, 0, 0, 0.2);\n        border: 2px solid var(--color-text-error);\n        border-radius: var(--border-radius);\n      }\n    }\n\n    .card__title-section {\n      padding-top: 0;\n      width: 100%;\n\n      .card__title-text {\n        width: 100%;\n        .card__title {\n          display: flex;\n          align-items: center;\n          background-color: var(--color-header-button);\n          border-radius: var(--border-radius) var(--border-radius) 0 0;\n          padding: var(--spacing-xxs);\n          min-width: 100%;\n          margin-top: calc(var(--spacing-s) * -1);\n          margin-left: calc(var(--spacing-s) * -1);\n          margin-right: calc(var(--spacing-s) * -1);\n          box-sizing: content-box;\n\n          span {\n            margin-bottom: -4px;\n          }\n\n          svg {\n            margin-bottom: 0;\n            background: var(--color-background);\n            padding: 4px;\n            border-radius: var(--border-radius);\n            box-sizing: content-box;\n          }\n\n          button {\n            height: 27px;\n            font-size: var(--font-body);\n            padding: 0 var(--spacing-xxs);\n            margin-left: auto;\n            background: unset !important;\n            border: 1px solid var(--color-text);\n            user-select: none;\n            span {\n              margin: 0;\n              color: var(--color-text) !important;\n\n              svg {\n                color: var(--color-text) !important;\n                background: none;\n                padding-left: 0;\n                padding-top: 2px;\n              }\n            }\n\n            &:hover {\n              background: var(--color-text) !important;\n              span:not(.button__label) {\n                filter: invert(1) !important;\n              }\n            }\n          }\n\n          .refresh-balance {\n            display: flex;\n            justify-content: center;\n\n            svg {\n              stroke: var(--color-text) !important;\n              background: unset;\n            }\n\n            &.refresh-balance--loading {\n              animation: rotate 1s linear infinite;\n              opacity: 0.8;\n\n              &:hover {\n                cursor: default;\n                svg {\n                  stroke: var(--color-text) !important;\n                }\n              }\n            }\n\n            &:hover {\n              cursor: pointer;\n              background: unset !important;\n              svg {\n                stroke: var(--color-primary) !important;\n              }\n            }\n          }\n\n          filter: invert(0) !important;\n        }\n        .card__subtitle {\n          min-height: 48px;\n          padding-bottom: var(--spacing-s);\n          box-sizing: content-box;\n        }\n      }\n    }\n\n    .button--secondary {\n      background-color: var(--color-header-button);\n    }\n\n    .card__main-actions {\n      display: flex;\n      flex-direction: column;\n      height: 100%;\n      gap: var(--spacing-m);\n      border-top: unset;\n    }\n\n    .card__first-pane {\n      display: flex;\n      flex-direction: column;\n      height: 100%;\n    }\n\n    .section__actions {\n      background: var(--color-background);\n      padding: var(--spacing-s);\n      border-radius: var(--border-radius);\n      margin-top: auto;\n    }\n    .section__title--small {\n      display: flex;\n      font-weight: 600;\n      margin-top: 0;\n\n      a {\n        color: var(--color-primary);\n        margin-left: auto;\n        font-weight: 600;\n        &:hover {\n          cursor: pointer;\n          color: var(--color-secondary);\n        }\n      }\n\n      .learn-more {\n        display: flex;\n        justify-content: center;\n        align-items: center;\n        margin-left: auto;\n        font-size: var(--font-xxsmall);\n        background: var(--color-background);\n        //margin-top: -12px;\n        padding-right: var(--spacing-s);\n        border-radius: var(--border-radius) var(--border-radius) 0 0;\n\n        svg {\n          width: var(--font-xsmall);\n          height: var(--font-xsmall);\n          color: var(--color-primary);\n          margin-left: var(--spacing-s);\n          margin-right: var(--spacing-xxxxs);\n          margin-top: -2px;\n        }\n\n        &:hover {\n          a {\n            color: var(--color-secondary);\n          }\n          svg {\n            color: var(--color-secondary);\n          }\n        }\n      }\n    }\n  }\n\n  .wallet {\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n  }\n\n  .network-selector {\n    svg {\n      // color:var(--color-text);\n    }\n  }\n\n  .help {\n    a {\n      color: var(--color-primary);\n      &:hover {\n        color: var(--color-secondary);\n      }\n    }\n  }\n}\n\n@keyframes rotate {\n  0% {\n    transform: rotate(0deg);\n  }\n  100% {\n    transform: rotate(360deg);\n  }\n}\n"],"sourceRoot":""}]);
